import Countup from 'vux/src/components/countup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
name: 'task',
components:{
Flexbox, FlexboxItem,Countup,TransferDom,Confirm
},
directives: {
TransferDom
},
data(){
return{
Scholarship:123.1,
confirmStatus:false,
confirmContent:'您尚未完成任务，无法提现，是否前往完成任务？',
}
},
methods:{
goPage(name){
this.$router.push({
name
})
},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
onCancel () {
console.log('on cancel')
},
onConfirm () {
console.log('on confirm')
this.$router.push({
name:'cashOut'
})
}
}
}



